/*RESET*/
*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

h1 {
  margin-bottom: 25px;
  font-size: calc(2rem + 8px);
  font-weight: 700;
  line-height: calc(3rem + 2px);
  letter-spacing: -0.8px;
  white-space: nowrap;
}

p {
  font-weight: 400;
  font-size: 1rem;
  color: #737373;
  line-height: calc(1rem + 9px);
  letter-spacing: 0px;
}

.container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  column-gap: 1rem;
  margin: 0 auto;
  padding: 80px 30px;
  max-width: 1380px;
  min-height: 100vh;
  font-family: "Cabin", sans-serif;
  font-optical-sizing: auto;
  font-style: normal;
  font-variation-settings: "wdth" 100;
  background-color: rgba(46, 196, 182, 0.1);
}

@media screen and (max-width: 860px) {
  .container {
    grid-template-columns: 1fr;
    max-width: 600px;
    padding: 40px 15px;
  }
}
.hero__section {
  position: relative;
  max-width: 430px;
}

.logo__frame {
  margin-bottom: 50px;
  border: none;
  border-radius: 50%;
  width: 5rem;
  height: 5rem;
  background-image: linear-gradient(to right, #F37920, #FFC624, #9ECD43, #3CBDB2);
}

.hero__title {
  font-size: 40px;
  margin-bottom: 20px;
}

@media screen and (min-width: 859px) {
  .hero__section {
    top: -100px;
  }
}
@media screen and (max-width: 860px) {
  .hero__section {
    margin-bottom: 40px;
  }
}
.form__page {
  padding: 2rem;
  max-width: 600px;
  background-color: #FFFFFF;
  box-shadow: 0px 5px 30px 0px rgba(4, 8, 19, 0.1019607843);
}

.form-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 1.5rem;
  row-gap: 2rem;
}

.form--flex {
  display: flex;
  flex-flow: column nowrap;
  gap: 0.5rem;
}

.form__input {
  width: 100%;
  min-width: 150px;
  border: 1px solid #F0F0F0;
  padding: 1rem;
  transition: all 0.2s ease-in-out;
}

.form__input:focus {
  outline: 2px solid #2EC4B6;
  box-shadow: 0 0 5px 1px #2EC4B6;
  transform: translateY(-2px);
}

label, .form__input {
  font-size: calc(1rem - 2px);
}

label {
  font-weight: 600;
  color: #011C34;
  line-height: 100%;
  letter-spacing: 0.1px;
}

.form__input {
  font-weight: 400;
  color: #011C34;
  background-color: transparent;
}

.form__input::placeholder {
  font-weight: 300;
  color: #737373;
  letter-spacing: 0.2px;
  line-height: calc(1rem + 4px);
}

.form--flex:nth-child(7), .form--flex:nth-child(8), .form--flex:last-child {
  grid-column: 1/3;
}

select option:disabled {
  color: red;
}

.submit__btn {
  border: none;
  border-radius: 2px;
  padding-block: 1rem;
  font-size: 1rem;
  font-weight: 600;
  background-color: #2EC4B6;
  color: #FFFFFF;
  line-height: 100%;
  letter-spacing: 0.2px;
  box-shadow: 0px 1px 20px 0px rgba(0, 0, 0, 0.256);
}

.submit__btn:hover {
  outline: 2px solid #2EC4B6;
  box-shadow: 0 0 5px 1px #2EC4B6;
  transform: translateY(-2px);
}

.submit__btn:active {
  outline: none;
  box-shadow: none;
  transform: translateY(0px);
}

.footer__text {
  text-align: center;
  font-size: calc(1rem - 2px);
  letter-spacing: 0px;
}

.footer__text a {
  font-size: 400;
  color: #2EC4B6;
}

@media screen and (max-width: 460px) {
  .form-grid {
    grid-template-columns: 1fr;
    column-gap: 0px;
  }
  .form--flex:nth-child(7), .form--flex:nth-child(8), .form--flex:last-child {
    grid-column: 1/2;
  }
}